// components/UserSettingsModal.jsx
import React, { useState, useEffect } from 'react';
import AV from 'leancloud-storage';
import './UserSettingsModal.css';

const UserSettingsModal = ({ isOpen, onClose }) => {
  const [currentUser, setCurrentUser] = useState(null);
  const [activeTab, setActiveTab] = useState('profile');
  const [loading, setLoading] = useState(false);
  const [saving, setSaving] = useState(false);
  const [message, setMessage] = useState({ type: '', text: '' });
  const [avatarFile, setAvatarFile] = useState(null);
  const [avatarPreview, setAvatarPreview] = useState('');

  // 
  const [studyStats, setStudyStats] = useState({
    totalCategories: 0,
    totalQuestions: 0,
    reviewQuestions: 0,
    todayReviewed: 0
  });

  // 
  const [profileForm, setProfileForm] = useState({
    username: '',
    email: '',
    nickname: '',
    bio: ''
  });

  const [passwordForm, setPasswordForm] = useState({
    currentPassword: '',
    newPassword: '',
    confirmPassword: ''
  });

  const [preferencesForm, setPreferencesForm] = useState({
    theme: 'light',
    language: 'zh-CN',
    notifications: true,
    emailUpdates: false
  });

  const [securityForm, setSecurityForm] = useState({
    emailVerified: false
  });

  // 
  const debugUserInfo = (user) => {
    if (!user) return;
   
  };

  useEffect(() => {
    if (isOpen) {
      const user = AV.User.current();
      setCurrentUser(user);
      if (user) {
        debugUserInfo(user);
        loadUserData(user);
        loadStudyStats(user);
      }
    }
  }, [isOpen]);

  const loadUserData = async (user) => {
    try {
      // 
      const freshUser = await user.fetch();
      
    

      setProfileForm({
        username: freshUser.getUsername() || '',
        email: freshUser.getEmail() || '',
        nickname: freshUser.get('nickname') || '',
        bio: freshUser.get('bio') || ''
      });

      setPreferencesForm({
        theme: freshUser.get('theme') || 'light',
        language: freshUser.get('language') || 'zh-CN',
        notifications: freshUser.get('notifications') !== false,
        emailUpdates: freshUser.get('emailUpdates') || false
      });

      setSecurityForm({
        emailVerified: freshUser.get('emailVerified') || false
      });

      //  - 
      const avatar = freshUser.get('avatar');
      if (avatar && typeof avatar === 'string') {
        const timestamp = new Date().getTime();
        const avatarUrlWithCacheBust = `${avatar}?t=${timestamp}`;
        setAvatarPreview(avatarUrlWithCacheBust);
      } else {
        setAvatarPreview('');
      }

    } catch (error) {
      console.error(':', error);
    }
  };

  const loadStudyStats = async (user) => {
    try {
      // 
      const categoriesQuery = new AV.Query('Category');
      categoriesQuery.equalTo('createdBy', user);
      const categories = await categoriesQuery.find();

      // 
      const questionsQuery = new AV.Query('Question');
      questionsQuery.equalTo('createdBy', user);
      const questions = await questionsQuery.find();

      // 
      const now = new Date();
      const reviewThreshold = 1;
      const reviewQuestions = questions.filter(question => {
        const lastReviewed = new Date(question.get('lastReviewedAt') || question.get('createdAt'));
        const daysSinceReview = Math.floor((now - lastReviewed) / (1000 * 60 * 60 * 24));
        return daysSinceReview >= reviewThreshold;
      });

      // 
      const today = new Date();
      today.setHours(0, 0, 0, 0);
      const todayReviewed = questions.filter(question => {
        const lastReviewed = new Date(question.get('lastReviewedAt'));
        return lastReviewed >= today;
      }).length;

      setStudyStats({
        totalCategories: categories.length,
        totalQuestions: questions.length,
        reviewQuestions: reviewQuestions.length,
        todayReviewed: todayReviewed
      });
    } catch (error) {
      console.error(':', error);
      setStudyStats({
        totalCategories: 0,
        totalQuestions: 0,
        reviewQuestions: 0,
        todayReviewed: 0
      });
    }
  };

  const showMessage = (type, text, duration = 3000) => {
    setMessage({ type, text });
    setTimeout(() => setMessage({ type: '', text: '' }), duration);
  };

  // 
  const handleAvatarUpload = (event) => {
    const file = event.target.files[0];
    if (!file) return;

    // 
    if (!file.type.startsWith('image/')) {
      showMessage('error', 'JPGPNGGIF');
      return;
    }

    // 
    if (file.size > 2 * 1024 * 1024) {
      showMessage('error', ' 2MB');
      return;
    }

    setAvatarFile(file);
    
    // 
    const reader = new FileReader();
    reader.onload = (e) => {
      setAvatarPreview(e.target.result);
    };
    reader.readAsDataURL(file);
  };

  const handleAvatarSave = async () => {
    if (!avatarFile || !currentUser) {
      showMessage('error', '');
      return;
    }

    setSaving(true);
    try {

      // 
      setAvatarPreview('');
      
      //  base64
      const fileData = await new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = (e) => {
          const base64Data = e.target.result.split(',')[1];
          resolve(base64Data);
        };
        reader.onerror = () => reject(new Error(''));
        reader.readAsDataURL(avatarFile);
      });

      // 
      const result = await AV.Cloud.run('updateUserAvatar', {
        fileData: fileData,
        fileName: avatarFile.name,
        mimeType: avatarFile.type
      });

      if (result.success) {
        showMessage('success', '');
        
        // 
        const timestamp = new Date().getTime();
        const avatarUrlWithCacheBust = `${result.avatarUrl}?t=${timestamp}`;
        
        console.log('URL:', avatarUrlWithCacheBust);
        
        // 
        setAvatarPreview(avatarUrlWithCacheBust);
        
        // 
        setAvatarFile(null);
        
        // 
        setTimeout(async () => {
          try {
            const updatedUser = await AV.User.current().fetch();
            setCurrentUser(updatedUser);
            console.log('');
            
            //  Navigation 
            window.dispatchEvent(new CustomEvent('userAvatarUpdated'));
          } catch (fetchError) {
            console.error(':', fetchError);
          }
        }, 1000);
        
      } else {
        throw new Error(result.message || '');
      }
      
    } catch (error) {
      console.error(' :', error);
      showMessage('error', `: ${error.message}`);
    } finally {
      setSaving(false);
    }
  };

  const handleProfileUpdate = async (e) => {
    e.preventDefault();
    if (!currentUser) return;

    setSaving(true);
    try {
      console.log('...', {
        nickname: profileForm.nickname,
        bio: profileForm.bio
      });

      // 
      const result = await AV.Cloud.run('updateUserProfile', {
        nickname: profileForm.nickname,
        bio: profileForm.bio
      });

      if (result.success) {
        showMessage('success', '');
        
        // 
        setTimeout(async () => {
          try {
            const updatedUser = await AV.User.current().fetch();
            setCurrentUser(updatedUser);
            console.log('');
          } catch (fetchError) {
            console.error(':', fetchError);
          }
        }, 500);
      } else {
        throw new Error(result.message || '');
      }
    } catch (error) {
      console.error(':', error);
      
      let errorMessage = '';
      if (error.code === 401) {
        errorMessage = '';
      } else if (error.message.includes('network')) {
        errorMessage = '';
      } else {
        errorMessage = `: ${error.message}`;
      }
      
      showMessage('error', errorMessage);
    } finally {
      setSaving(false);
    }
  };

  const handlePasswordChange = async (e) => {
    e.preventDefault();
    
    if (passwordForm.newPassword !== passwordForm.confirmPassword) {
      showMessage('error', '');
      return;
    }

    if (passwordForm.newPassword.length < 6) {
      showMessage('error', '6');
      return;
    }

    setSaving(true);
    try {
      await currentUser.updatePassword(
        passwordForm.currentPassword,
        passwordForm.newPassword
      );
      
      showMessage('success', '');
      setPasswordForm({
        currentPassword: '',
        newPassword: '',
        confirmPassword: ''
      });
    } catch (error) {
      console.error(':', error);
      showMessage('error', `: ${error.message}`);
    } finally {
      setSaving(false);
    }
  };

  const handlePreferencesUpdate = async (e) => {
    e.preventDefault();
    if (!currentUser) return;

    setSaving(true);
    try {
      console.log('...', preferencesForm);

      // 
      const result = await AV.Cloud.run('updateUserPreferences', {
        theme: preferencesForm.theme,
        language: preferencesForm.language,
        notifications: preferencesForm.notifications,
        emailUpdates: preferencesForm.emailUpdates
      });

      if (result.success) {
        showMessage('success', '');
        
        // 
        setTimeout(async () => {
          try {
            const updatedUser = await AV.User.current().fetch();
            setCurrentUser(updatedUser);
          } catch (fetchError) {
            console.error(':', fetchError);
          }
        }, 500);
      } else {
        throw new Error(result.message || '');
      }
    } catch (error) {
      console.error(':', error);
      showMessage('error', `: ${error.message}`);
    } finally {
      setSaving(false);
    }
  };

  // 
  const handleEmailVerification = async () => {
    if (!currentUser || !currentUser.getEmail()) {
      showMessage('error', '');
      return;
    }

    setLoading(true);
    try {
      await AV.User.requestEmailVerify(currentUser.getEmail());
      showMessage('success', '');
    } catch (error) {
      console.error(':', error);
      showMessage('error', `: ${error.message}`);
    } finally {
      setLoading(false);
    }
  };

  // 
  const handlePasswordReset = async () => {
    if (!currentUser || !currentUser.getEmail()) {
      showMessage('error', '');
      return;
    }

    setLoading(true);
    try {
      await AV.User.requestPasswordReset(currentUser.getEmail());
      showMessage('success', '');
    } catch (error) {
      console.error(':', error);
      showMessage('error', `: ${error.message}`);
    } finally {
      setLoading(false);
    }
  };

  // 
  const handleDataExport = async () => {
    setLoading(true);
    try {
      const [categories, questions] = await Promise.all([
        new AV.Query('Category')
          .equalTo('createdBy', currentUser)
          .find(),
        new AV.Query('Question')
          .equalTo('createdBy', currentUser)
          .include('category')
          .find()
      ]);

      const exportData = {
        exportTime: new Date().toISOString(),
        user: {
          username: currentUser.getUsername(),
          email: currentUser.getEmail(),
          nickname: currentUser.get('nickname')
        },
        categories: categories.map(cat => ({
          id: cat.id,
          name: cat.get('name'),
          description: cat.get('description'),
          questionCount: cat.get('questionCount') || 0,
          createdAt: cat.get('createdAt'),
          updatedAt: cat.get('updatedAt')
        })),
        questions: questions.map(q => ({
          id: q.id,
          title: q.get('title'),
          detailedAnswer: q.get('detailedAnswer'),
          oralAnswer: q.get('oralAnswer'),
          code: q.get('code'),
          difficulty: q.get('difficulty'),
          proficiency: q.get('proficiency'),
          appearanceLevel: q.get('appearanceLevel'),
          tags: q.get('tags') || [],
          category: q.get('category') ? {
            id: q.get('category').id,
            name: q.get('category').get('name')
          } : null,
          createdAt: q.get('createdAt'),
          updatedAt: q.get('updatedAt')
        }))
      };

      const dataStr = JSON.stringify(exportData, null, 2);
      const dataBlob = new Blob([dataStr], { type: 'application/json' });
      const url = URL.createObjectURL(dataBlob);
      const link = document.createElement('a');
      link.href = url;
      link.download = `knowledge-base-export-${new Date().toISOString().split('T')[0]}.json`;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      URL.revokeObjectURL(url);

      showMessage('success', '');
    } catch (error) {
      console.error(':', error);
      showMessage('error', `: ${error.message}`);
    } finally {
      setLoading(false);
    }
  };

  const handleInputChange = (formType, field, value) => {
    switch (formType) {
      case 'profile':
        setProfileForm(prev => ({ ...prev, [field]: value }));
        break;
      case 'password':
        setPasswordForm(prev => ({ ...prev, [field]: value }));
        break;
      case 'preferences':
        setPreferencesForm(prev => ({ ...prev, [field]: value }));
        break;
    }
  };

  if (!isOpen) return null;

  return (
    <div className="user-settings-overlay">
      <div className="user-settings-modal">
        {/*  */}
        <div className="settings-header">
          <div className="header-content">
            <h2></h2>
            <p></p>
          </div>
          <button 
            className="close-button"
            onClick={onClose}
            disabled={saving}
          >
            
          </button>
        </div>

        {/*  */}
        {message.text && (
          <div className={`message ${message.type}`}>
            {message.text}
          </div>
        )}

        <div className="settings-content">
          {/*  */}
          <div className="settings-sidebar">
            <button 
              className={`sidebar-item ${activeTab === 'profile' ? 'active' : ''}`}
              onClick={() => setActiveTab('profile')}
            >
              <span className="item-icon"></span>
              <span className="item-text"></span>
            </button>
            
            <button 
              className={`sidebar-item ${activeTab === 'security' ? 'active' : ''}`}
              onClick={() => setActiveTab('security')}
            >
              <span className="item-icon"></span>
              <span className="item-text"></span>
            </button>
            
            <button 
              className={`sidebar-item ${activeTab === 'preferences' ? 'active' : ''}`}
              onClick={() => setActiveTab('preferences')}
            >
              <span className="item-icon"></span>
              <span className="item-text"></span>
            </button>
            
            <button 
              className={`sidebar-item ${activeTab === 'data' ? 'active' : ''}`}
              onClick={() => setActiveTab('data')}
            >
              <span className="item-icon"></span>
              <span className="item-text"></span>
            </button>
          </div>

          {/*  */}
          <div className="settings-main">
            {activeTab === 'profile' && (
              <div className="tab-content">
                <h3></h3>
                
                {/*  */}
                <div className="avatar-section">
                  <div className="avatar-upload">
                    <div className="avatar-preview">
                      {avatarPreview ? (
                        <img 
                          src={avatarPreview} 
                          alt=""
                          style={{
                            width: '100%',
                            height: '100%',
                            objectFit: 'cover',
                            objectPosition: 'center'
                          }}
                          onLoad={() => console.log(' ')}
                          onError={(e) => {
                            console.error(' :', avatarPreview);
                            e.target.style.display = 'none';
                          }}
                        />
                      ) : (
                        <div className="avatar-placeholder">
                          {profileForm.username?.charAt(0).toUpperCase()}
                        </div>
                      )}
                    </div>
                    <div className="avatar-controls">
                      <input
                        type="file"
                        id="avatar-upload"
                        accept="image/*"
                        onChange={handleAvatarUpload}
                        className="avatar-input"
                      />
                      <label htmlFor="avatar-upload" className="avatar-upload-btn">
                        
                      </label>
                      {avatarFile && (
                        <button 
                          className="avatar-save-btn"
                          onClick={handleAvatarSave}
                          disabled={saving}
                        >
                          {saving ? '...' : ''}
                        </button>
                      )}
                    </div>
                  </div>
                  <div className="avatar-hint">
                     JPGPNG  2MB
                  </div>
                </div>

                <form onSubmit={handleProfileUpdate} className="settings-form">
                  <div className="form-group">
                    <label></label>
                    <input
                      type="text"
                      value={profileForm.username}
                      disabled
                      className="disabled-input"
                    />
                    <div className="field-hint"></div>
                  </div>

                  <div className="form-group">
                    <label></label>
                    <input
                      type="email"
                      value={profileForm.email}
                      disabled
                      className="disabled-input"
                    />
                    <div className="field-hint">
                      {profileForm.email ? '' : ''}
                    </div>
                  </div>

                  <div className="form-group">
                    <label></label>
                    <input
                      type="text"
                      value={profileForm.nickname}
                      onChange={(e) => handleInputChange('profile', 'nickname', e.target.value)}
                      placeholder=""
                      maxLength={20}
                    />
                  </div>

                  <div className="form-group">
                    <label></label>
                    <textarea
                      value={profileForm.bio}
                      onChange={(e) => handleInputChange('profile', 'bio', e.target.value)}
                      placeholder="..."
                      rows="3"
                      maxLength={200}
                    />
                    <div className="char-count">
                      {profileForm.bio.length}/200
                    </div>
                  </div>

                  <div className="form-actions">
                    <button 
                      type="submit" 
                      className="save-button"
                      disabled={saving}
                    >
                      {saving ? '...' : ''}
                    </button>
                  </div>
                </form>
              </div>
            )}

            {activeTab === 'security' && (
              <div className="tab-content">
                <h3></h3>
                
                {/*  */}
                <div className="security-section">
                  <h4></h4>
                  <div className="security-item">
                    <div className="security-info">
                      <span className="security-label"></span>
                      <span className={`security-status ${securityForm.emailVerified ? 'verified' : 'unverified'}`}>
                        {securityForm.emailVerified ? '' : ''}
                      </span>
                    </div>
                    {!securityForm.emailVerified && (
                      <button 
                        className="security-action-btn"
                        onClick={handleEmailVerification}
                        disabled={loading || !profileForm.email}
                      >
                        {loading ? '...' : ''}
                      </button>
                    )}
                  </div>
                </div>

                {/*  */}
                <div className="security-section">
                  <h4></h4>
                  <form onSubmit={handlePasswordChange} className="settings-form">
                    <div className="form-group">
                      <label></label>
                      <input
                        type="password"
                        value={passwordForm.currentPassword}
                        onChange={(e) => handleInputChange('password', 'currentPassword', e.target.value)}
                        placeholder=""
                        required
                      />
                    </div>

                    <div className="form-group">
                      <label></label>
                      <input
                        type="password"
                        value={passwordForm.newPassword}
                        onChange={(e) => handleInputChange('password', 'newPassword', e.target.value)}
                        placeholder="6"
                        minLength={6}
                        required
                      />
                    </div>

                    <div className="form-group">
                      <label></label>
                      <input
                        type="password"
                        value={passwordForm.confirmPassword}
                        onChange={(e) => handleInputChange('password', 'confirmPassword', e.target.value)}
                        placeholder=""
                        minLength={6}
                        required
                      />
                    </div>

                    <div className="password-strength">
                      <div className="strength-bar">
                        <div 
                          className={`strength-fill ${
                            passwordForm.newPassword.length >= 6 ? 'strong' : 'weak'
                          }`}
                          style={{ 
                            width: `${Math.min((passwordForm.newPassword.length / 6) * 100, 100)}%` 
                          }}
                        ></div>
                      </div>
                      <div className="strength-text">
                        {passwordForm.newPassword.length >= 6 ? '' : '6'}
                      </div>
                    </div>

                    <div className="form-actions">
                      <button 
                        type="submit" 
                        className="save-button"
                        disabled={saving}
                      >
                        {saving ? '...' : ''}
                      </button>
                    </div>
                  </form>
                </div>

                {/*  */}
                <div className="security-section">
                  <h4></h4>
                  <div className="security-item">
                    <div className="security-info">
                      <span className="security-label"></span>
                      <span className="security-description">
                        
                      </span>
                    </div>
                    <button 
                      className="security-action-btn secondary"
                      onClick={handlePasswordReset}
                      disabled={loading || !profileForm.email}
                    >
                      {loading ? '...' : ''}
                    </button>
                  </div>
                </div>
              </div>
            )}

            {activeTab === 'preferences' && (
              <div className="tab-content">
                <h3></h3>
                <form onSubmit={handlePreferencesUpdate} className="settings-form">
                  <div className="form-group">
                    <label></label>
                    <div className="radio-group">
                      <label className="radio-option">
                        <input
                          type="radio"
                          name="theme"
                          value="light"
                          checked={preferencesForm.theme === 'light'}
                          onChange={(e) => handleInputChange('preferences', 'theme', e.target.value)}
                        />
                        <span className="radio-label">
                          <span className="radio-icon"></span>
                          
                        </span>
                      </label>
                      <label className="radio-option">
                        <input
                          type="radio"
                          name="theme"
                          value="dark"
                          checked={preferencesForm.theme === 'dark'}
                          onChange={(e) => handleInputChange('preferences', 'theme', e.target.value)}
                        />
                        <span className="radio-label">
                          <span className="radio-icon"></span>
                          
                        </span>
                      </label>
                    </div>
                  </div>

                  <div className="form-group">
                    <label></label>
                    <select
                      value={preferencesForm.language}
                      onChange={(e) => handleInputChange('preferences', 'language', e.target.value)}
                    >
                      <option value="zh-CN"></option>
                      <option value="en-US">English</option>
                    </select>
                  </div>

                  <div className="form-group">
                    <label></label>
                    <div className="switch-group">
                      <label className="switch">
                        <input
                          type="checkbox"
                          checked={preferencesForm.notifications}
                          onChange={(e) => handleInputChange('preferences', 'notifications', e.target.checked)}
                        />
                        <span className="slider"></span>
                      </label>
                      <span className="switch-label">
                        
                      </span>
                    </div>
                  </div>

                  <div className="form-group">
                    <label></label>
                    <div className="switch-group">
                      <label className="switch">
                        <input
                          type="checkbox"
                          checked={preferencesForm.emailUpdates}
                          onChange={(e) => handleInputChange('preferences', 'emailUpdates', e.target.checked)}
                        />
                        <span className="slider"></span>
                      </label>
                      <span className="switch-label">
                        
                      </span>
                    </div>
                  </div>

                  <div className="form-actions">
                    <button 
                      type="submit" 
                      className="save-button"
                      disabled={saving}
                    >
                      {saving ? '...' : ''}
                    </button>
                  </div>
                </form>
              </div>
            )}

            {activeTab === 'data' && (
              <div className="tab-content">
                <h3></h3>
                
                {/*  HomePage  */}
                <div className="stats-overview">
                  <div className="stats-grid">
                    <div className="stat-card">
                      <div className="stat-icon"></div>
                      <div className="stat-content">
                        <div className="stat-number">{studyStats.totalCategories}</div>
                        <div className="stat-label"></div>
                      </div>
                    </div>
                    
                    <div className="stat-card">
                      <div className="stat-icon"></div>
                      <div className="stat-content">
                        <div className="stat-number">{studyStats.totalQuestions}</div>
                        <div className="stat-label"></div>
                      </div>
                    </div>
                    
                    <div className="stat-card">
                      <div className="stat-icon"></div>
                      <div className="stat-content">
                        <div className="stat-number">{studyStats.reviewQuestions}</div>
                        <div className="stat-label"></div>
                      </div>
                    </div>
                    
                    <div className="stat-card">
                      <div className="stat-icon"></div>
                      <div className="stat-content">
                        <div className="stat-number">{studyStats.todayReviewed}</div>
                        <div className="stat-label"></div>
                      </div>
                    </div>
                  </div>
                </div>

                {/*  */}
                <div className="progress-section">
                  <div className="progress-item">
                    <div className="progress-header">
                      <span className="progress-label"></span>
                      <span className="progress-percent">
                        {studyStats.totalQuestions > 0 
                          ? Math.round(((studyStats.totalQuestions - studyStats.reviewQuestions) / studyStats.totalQuestions) * 100)
                          : 0
                        }%
                      </span>
                    </div>
                    <div className="progress-bar">
                      <div 
                        className="progress-fill"
                        style={{ 
                          width: `${studyStats.totalQuestions > 0 
                            ? ((studyStats.totalQuestions - studyStats.reviewQuestions) / studyStats.totalQuestions) * 100 
                            : 0
                          }%` 
                        }}
                      ></div>
                    </div>
                    <div className="progress-text">
                       {studyStats.totalQuestions - studyStats.reviewQuestions} / {studyStats.totalQuestions} 
                    </div>
                  </div>
                </div>

                {/*  */}
                <div className="data-section">
                  <h4></h4>
                  <div className="data-item">
                    <div className="data-info">
                      <span className="data-label"></span>
                      <span className="data-description">
                         JSON 
                      </span>
                    </div>
                    <button 
                      className="data-action-btn"
                      onClick={handleDataExport}
                      disabled={loading}
                    >
                      {loading ? '...' : ''}
                    </button>
                  </div>
                </div>

                {/*  */}
                <div className="data-section">
                  <h4></h4>
                  <div className="data-item danger">
                    <div className="data-info">
                      <span className="data-label"></span>
                      <span className="data-description">
                        
                      </span>
                    </div>
                    <button 
                      className="data-action-btn danger"
                      disabled
                    >
                      
                    </button>
                  </div>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default UserSettingsModal;