// components/community/SearchBar.jsx
import React, { useState, useRef, useEffect } from 'react';
import './SearchBar.css';

const SearchBar = ({ onSearch, placeholder = "..." }) => {
  const [keyword, setKeyword] = useState('');
  const [isFocused, setIsFocused] = useState(false);
  const [showSuggestions, setShowSuggestions] = useState(false);
  const searchInputRef = useRef(null);

  // 
  const popularSuggestions = [
    'JavaScript',
    'React',
    '',
    'LeetCode',
    '',
    '',
    'TypeScript',
    'Vue'
  ];

  useEffect(() => {
    // 
    const timeoutId = setTimeout(() => {
      onSearch(keyword);
    }, 300);

    return () => clearTimeout(timeoutId);
  }, [keyword, onSearch]);

  const handleSearch = (value) => {
    setKeyword(value);
    setShowSuggestions(value.length > 0);
  };

  const handleClear = () => {
    setKeyword('');
    setShowSuggestions(false);
    onSearch('');
    searchInputRef.current?.focus();
  };

  const handleSuggestionClick = (suggestion) => {
    setKeyword(suggestion);
    setShowSuggestions(false);
    onSearch(suggestion);
  };

  const handleFocus = () => {
    setIsFocused(true);
    if (keyword.length > 0) {
      setShowSuggestions(true);
    }
  };

  const handleBlur = () => {
    setIsFocused(false);
    // 
    setTimeout(() => setShowSuggestions(false), 200);
  };

  const handleKeyDown = (e) => {
    if (e.key === 'Escape') {
      setShowSuggestions(false);
      searchInputRef.current?.blur();
    } else if (e.key === 'Enter') {
      setShowSuggestions(false);
      onSearch(keyword);
    }
  };

  return (
    <div className="search-bar">
      <div className={`search-input-container ${isFocused ? 'focused' : ''}`}>
        <div className="search-icon"></div>
        
        <input
          ref={searchInputRef}
          type="text"
          placeholder={placeholder}
          value={keyword}
          onChange={(e) => handleSearch(e.target.value)}
          onFocus={handleFocus}
          onBlur={handleBlur}
          onKeyDown={handleKeyDown}
          className="search-input"
        />

        {keyword && (
          <button 
            className="clear-search"
            onClick={handleClear}
            type="button"
          >
            
          </button>
        )}

        {/*  */}
        {showSuggestions && (
          <div className="search-suggestions">
            <div className="suggestions-header">
              <span className="suggestions-title"></span>
              <button 
                className="close-suggestions"
                onClick={() => setShowSuggestions(false)}
              >
                
              </button>
            </div>
            
            <div className="suggestions-list">
              {/*  */}
              {keyword.length > 0 && (
                <div 
                  className="suggestion-item exact-match"
                  onClick={() => handleSuggestionClick(keyword)}
                >
                  <span className="suggestion-icon"></span>
                  <span className="suggestion-text">: "{keyword}"</span>
                </div>
              )}

              {/*  */}
              <div className="suggestions-section">
                <div className="section-title"></div>
                {popularSuggestions
                  .filter(suggestion => 
                    suggestion.toLowerCase().includes(keyword.toLowerCase())
                  )
                  .map((suggestion, index) => (
                    <div
                      key={index}
                      className="suggestion-item"
                      onClick={() => handleSuggestionClick(suggestion)}
                    >
                      <span className="suggestion-icon"></span>
                      <span className="suggestion-text">{suggestion}</span>
                    </div>
                  ))
                }
              </div>

              {/*  */}
              <div className="search-tips">
                <div className="tips-title"></div>
                <div className="tips-list">
                  <div className="tip-item">
                    <span className="tip-icon"></span>
                    <span></span>
                  </div>
                  <div className="tip-item">
                    <span className="tip-icon"></span>
                    <span> Enter </span>
                  </div>
                  <div className="tip-item">
                    <span className="tip-icon"></span>
                    <span> ESC </span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>

      {/*  */}
      {keyword && (
        <div className="search-stats">
          <span className="stats-text">
            : <strong>"{keyword}"</strong>
          </span>
          <button 
            className="advanced-search-btn"
            onClick={() => setShowSuggestions(true)}
          >
            
          </button>
        </div>
      )}
    </div>
  );
};

export default SearchBar;