// components/Chatbox.jsx
import React, { useState, useRef, useEffect } from 'react';
import DialogflowService from '../services/dialogflowService';
import './Chatbox.css';

const Chatbox = ({ onNavigate, categories, questions, currentUser }) => {
  const [isOpen, setIsOpen] = useState(false);
  const [messages, setMessages] = useState([]);
  const [inputText, setInputText] = useState('');
  const [isTyping, setIsTyping] = useState(false);
  const messagesEndRef = useRef(null);

  // 
  useEffect(() => {
    if (isOpen && messages.length === 0) {
      sendWelcomeMessage();
    }
  }, [isOpen]);

  const sendWelcomeMessage = async () => {
    setIsTyping(true);
    
    try {
      //  API
      const response = await DialogflowService.sendWelcome(currentUser?.id);
      
      const welcomeMessage = {
        id: Date.now(),
        text: response.text,
        isBot: true,
        timestamp: new Date(),
        quickReplies: [
          '',
          '',
          '',
          ''
        ]
      };

      setMessages([welcomeMessage]);
    } catch (error) {
      console.error('Welcome message error:', error);
      // 
      const fallbackMessage = {
        id: 1,
        text: ` ${currentUser ? ` ${currentUser.getUsername()}` : ''}\n\n \n \n \n \n\n`,
        isBot: true,
        timestamp: new Date(),
        quickReplies: [
          '',
          '',
          '',
          ''
        ]
      };
      setMessages([fallbackMessage]);
    } finally {
      setIsTyping(false);
    }
  };

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  // 
  const processMessage = async (userMessage) => {
    setIsTyping(true);
    
    try {
      const response = await DialogflowService.detectIntent(userMessage, currentUser?.id);
      
      setIsTyping(false);
      
      return {
        text: response.text,
        quickReplies: [
          '',
          '', 
          '',
          ''
        ],
        actions: []
      };
      
    } catch (error) {
      console.error('Message processing error:', error);
      setIsTyping(false);
      
      return {
        text: '',
        quickReplies: ['', '', '', '']
      };
    }
  };

  const handleSendMessage = async () => {
    if (!inputText.trim()) return;

    const userMessage = {
      id: Date.now(),
      text: inputText,
      isBot: false,
      timestamp: new Date()
    };

    setMessages(prev => [...prev, userMessage]);
    setInputText('');

    // 
    const botResponse = await processMessage(inputText);
    
    const botMessage = {
      id: Date.now() + 1,
      text: botResponse.text,
      isBot: true,
      timestamp: new Date(),
      actions: botResponse.actions,
      quickReplies: botResponse.quickReplies
    };

    setMessages(prev => [...prev, botMessage]);
  };

  const handleQuickReply = (text) => {
    setInputText(text);
    setTimeout(() => {
      handleSendMessage();
    }, 100);
  };

  const handleActionClick = (action) => {
    if (action.type === 'navigate' && onNavigate) {
      onNavigate(action.target);
      
      // 
      const confirmMessage = {
        id: Date.now(),
        text: `  ${action.label}...`,
        isBot: true,
        timestamp: new Date()
      };
      setMessages(prev => [...prev, confirmMessage]);
    } else if (action.type === 'function') {
      // 
      handleFunctionAction(action);
    }
  };

  const handleFunctionAction = (action) => {
    switch (action.target) {
      case 'searchQuestions':
        // 
        const searchMessage = {
          id: Date.now(),
          text: '',
          isBot: true,
          timestamp: new Date()
        };
        setMessages(prev => [...prev, searchMessage]);
        break;
      default:
        console.log('Function action:', action);
    }
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSendMessage();
    }
  };

  // 
  const clearConversation = () => {
    setMessages([]);
    sendWelcomeMessage();
  };

  return (
    <>
      {/*  */}
      {!isOpen && (
        <button 
          className="chatbox-floating-btn"
          onClick={() => setIsOpen(true)}
          title="AI"
        >
          <span className="chatbot-icon"></span>
          <span className="pulse-dot"></span>
        </button>
      )}

      {/*  */}
      {isOpen && (
        <div className="chatbox-container">
          <div className="chatbox-header">
            <div className="chatbox-title">
              <span className="bot-avatar"></span>
              <div className="title-text">
                <h4>AI</h4>
                <span className="status">AI</span>
              </div>
            </div>
            <div className="header-actions">
              <button 
                className="clear-btn"
                onClick={clearConversation}
                title=""
              >
                
              </button>
              <button 
                className="close-btn"
                onClick={() => setIsOpen(false)}
              >
                
              </button>
            </div>
          </div>

          <div className="chatbox-messages">
            {messages.map((message) => (
              <div
                key={message.id}
                className={`message ${message.isBot ? 'bot-message' : 'user-message'}`}
              >
                <div className="message-content">
                  <div className="message-text">
                    {message.text.split('\n').map((line, i) => (
                      <span key={i}>
                        {line}
                        <br />
                      </span>
                    ))}
                  </div>
                  <div className="message-time">
                    {message.timestamp.toLocaleTimeString('zh-CN', { 
                      hour: '2-digit', 
                      minute: '2-digit' 
                    })}
                  </div>
                  
                  {/*  */}
                  {message.quickReplies && (
                    <div className="quick-replies">
                      {message.quickReplies.map((reply, index) => (
                        <button
                          key={index}
                          className="quick-reply-btn"
                          onClick={() => handleQuickReply(reply)}
                        >
                          {reply}
                        </button>
                      ))}
                    </div>
                  )}
                  
                  {/*  */}
                  {message.actions && (
                    <div className="action-buttons">
                      {message.actions.map((action, index) => (
                        <button
                          key={index}
                          className="action-btn"
                          onClick={() => handleActionClick(action)}
                        >
                          {action.label}
                        </button>
                      ))}
                    </div>
                  )}
                </div>
              </div>
            ))}
            
            {isTyping && (
              <div className="message bot-message">
                <div className="message-content">
                  <div className="typing-indicator">
                    <span>AI</span>
                    <div className="typing-dots">
                      <span></span>
                      <span></span>
                      <span></span>
                    </div>
                  </div>
                </div>
              </div>
            )}
            
            <div ref={messagesEndRef} />
          </div>

          <div className="chatbox-input">
            <div className="input-container">
              <textarea
                value={inputText}
                onChange={(e) => setInputText(e.target.value)}
                onKeyPress={handleKeyPress}
                placeholder="AI..."
                rows="1"
                className="message-input"
              />
              <button 
                onClick={handleSendMessage}
                disabled={!inputText.trim() || isTyping}
                className="send-btn"
              >
                {isTyping ? '' : ''}
              </button>
            </div>
            <div className="input-hint">
               Enter Shift + Enter 
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default Chatbox;