// components/community/CommunityPage.jsx
import React, { useState, useEffect } from 'react';
import { CommunityService } from '../../services/communityService';
import CreatePostModal from './CreatePostModal';
import SearchBar from './SearchBar';
import TagCloud from './TagCloud';
import CommunityStats from './CommunityStats';
import PostCard from './PostCard';
import './CommunityPage.css';

const CommunityPage = () => {
  const [activeTab, setActiveTab] = useState('latest');
  const [posts, setPosts] = useState([]);
  const [allPosts, setAllPosts] = useState([]); // 
  const [loading, setLoading] = useState(false);
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [searchKeyword, setSearchKeyword] = useState('');
  const [pagination, setPagination] = useState({
    page: 1,
    pageSize: 20,
    hasMore: true
  });

  useEffect(() => {
    loadPosts(true);
  }, [activeTab]);

  // 
  useEffect(() => {
    if (searchKeyword.trim() === '') {
      // 
      setPosts(allPosts);
    } else {
      // 
      const filtered = allPosts.filter(post => 
        post.get('title')?.toLowerCase().includes(searchKeyword.toLowerCase())
      );
      setPosts(filtered);
    }
  }, [searchKeyword, allPosts]);

  const loadPosts = async (reset = false) => {
    if (loading) return;
    
    setLoading(true);
    try {
      const currentPage = reset ? 1 : pagination.page;
      const options = {
        page: currentPage,
        pageSize: pagination.pageSize,
        sortBy: activeTab === 'latest' ? 'createdAt' : 'likes',
        sortOrder: 'desc'
      };

      const postsData = await CommunityService.getPosts(options);
      
      if (reset) {
        setAllPosts(postsData);
        setPosts(postsData);
      } else {
        const newAllPosts = [...allPosts, ...postsData];
        setAllPosts(newAllPosts);
        
        // 
        if (searchKeyword.trim() === '') {
          setPosts(newAllPosts);
        } else {
          const filtered = newAllPosts.filter(post => 
            post.get('title')?.toLowerCase().includes(searchKeyword.toLowerCase())
          );
          setPosts(filtered);
        }
      }

      setPagination(prev => ({
        ...prev,
        page: currentPage,
        hasMore: postsData.length === pagination.pageSize
      }));
    } catch (error) {
      console.error(':', error);
    } finally {
      setLoading(false);
    }
  };

  const handleSearch = (keyword) => {
    setSearchKeyword(keyword);
  };

  const handleLoadMore = () => {
    if (!loading && pagination.hasMore) {
      loadPosts(false);
    }
  };

  const handlePostCreated = (newPost) => {
    // 
    setAllPosts(prev => [newPost, ...prev]);
    
    // 
    if (searchKeyword.trim() === '' || 
        newPost.get('title')?.toLowerCase().includes(searchKeyword.toLowerCase())) {
      setPosts(prev => [newPost, ...prev]);
    }
    
    setShowCreateModal(false);
  };

  // 
  const getSearchStats = () => {
    if (searchKeyword.trim() === '') {
      return ` ${allPosts.length} `;
    } else {
      const matchCount = allPosts.filter(post => 
        post.get('title')?.toLowerCase().includes(searchKeyword.toLowerCase())
      ).length;
      return ` ${posts.length}  ${matchCount} `;
    }
  };

  return (
    <section className="community-section">
      <div className="container">
        {/*  */}
        <div className="community-header">
          <div className="header-content">
            <div className="header-text">
              <h2></h2>
              <p></p>
            </div>
            <button 
              className="create-post-btn modern-btn primary"
              onClick={() => setShowCreateModal(true)}
            >
              <span className="btn-icon"></span>
              
            </button>
          </div>
        </div>

        {/*  */}
        <div className="community-toolbar">
          <div className="toolbar-left">
            <SearchBar 
              onSearch={handleSearch}
              placeholder="..."
            />
          </div>
          <div className="toolbar-right">
            <CommunityStats />
          </div>
        </div>

        {/*  */}
        {searchKeyword && (
          <div className="search-results-info">
            <div className="results-stats">
              {getSearchStats()}
              {searchKeyword && (
                <span className="search-keyword">
                  : <strong>"{searchKeyword}"</strong>
                </span>
              )}
            </div>
          </div>
        )}

        <div className="community-layout">
          {/*  */}
          <aside className="community-sidebar">
            <div className="sidebar-section">
              <h3></h3>
              <TagCloud 
                onTagClick={(tag) => handleSearch(tag)}
              />
            </div>
            
            <div className="sidebar-section">
              <h3></h3>
              <div className="community-guidelines">
                <p> </p>
                <p> </p>
                <p> </p>
                <p> </p>
              </div>
            </div>
          </aside>

          {/*  */}
          <main className="community-main">
            {/*  */}
            <div className="community-tabs">
              <button 
                className={`modern-tab ${activeTab === 'latest' ? 'active' : ''}`}
                onClick={() => setActiveTab('latest')}
              >
                <span className="tab-icon"></span>
                <span className="tab-text"></span>
              </button>
              <button 
                className={`modern-tab ${activeTab === 'popular' ? 'active' : ''}`}
                onClick={() => setActiveTab('popular')}
              >
                <span className="tab-icon"></span>
                <span className="tab-text"></span>
              </button>
            </div>
            
            {/*  */}
            <div className="post-list">
              {loading && (
                <div className="loading">
                  <div className="spinner"></div>
                  <span>...</span>
                </div>
              )}
              
              {posts.map(post => (
                <PostCard key={post.id} post={post} />
              ))}
              
              {!loading && posts.length === 0 && (
                <div className="empty-state">
                  <div className="empty-icon">
                    {searchKeyword ? '' : ''}
                  </div>
                  <h3>
                    {searchKeyword ? '' : ''}
                  </h3>
                  <p>
                    {searchKeyword 
                      ? ` "${searchKeyword}" `
                      : ''
                    }
                  </p>
                  {searchKeyword && (
                    <button 
                      onClick={() => handleSearch('')}
                      className="clear-search-btn"
                    >
                      
                    </button>
                  )}
                </div>
              )}
            </div>
            
            {pagination.hasMore && posts.length > 0 && searchKeyword === '' && (
              <div className="load-more-section">
                <button 
                  onClick={handleLoadMore}
                  disabled={loading}
                  className="load-more-btn"
                >
                  {loading ? '...' : ''}
                </button>
              </div>
            )}
          </main>
        </div>
        
        {/*  */}
        {showCreateModal && (
          <CreatePostModal 
            onClose={() => setShowCreateModal(false)}
            onSuccess={handlePostCreated}
          />
        )}
      </div>
    </section>
  );
};

export default CommunityPage;