// components/community/CommentItem.jsx
import React, { useState } from 'react';
import { CommunityService } from '../../services/communityService';
import AV from 'leancloud-storage';
import './CommentItem.css';

// 
const formatTime = (date) => {
  if (!date) return "";
  const now = new Date();
  const diffMs = now - new Date(date);
  const diffMins = Math.floor(diffMs / 60000);
  const diffHours = Math.floor(diffMs / 3600000);
  const diffDays = Math.floor(diffMs / 86400000);

  if (diffMins < 1) return "";
  if (diffMins < 60) return `${diffMins}`;
  if (diffHours < 24) return `${diffHours}`;
  if (diffDays < 7) return `${diffDays}`;
  return new Date(date).toLocaleDateString('zh-CN', {
    year: 'numeric',
    month: 'short',
    day: 'numeric'
  });
};

const CommentItem = ({ comment, onReply, onUpdate }) => {
  const [likes, setLikes] = useState(comment.get('likes') || 0);
  const [isLiked, setIsLiked] = useState(false);
  const [showActions, setShowActions] = useState(false);
  const [editing, setEditing] = useState(false);
  const [editContent, setEditContent] = useState(comment.get('content') || '');
  const [saving, setSaving] = useState(false);

  // 
  const getAvatarUrl = () => {
    return comment.get('author')?.get('avatar') || '/default-avatar.png';
  };

  const getUsername = () => {
    return comment.get('author')?.get('username') || '';
  };

  // 
  const isAuthor = () => {
    const currentUser = AV.User.current();
    return currentUser && comment.get('author')?.id === currentUser.id;
  };

  // 
  const handleLike = async () => {
    try {
      const result = await CommunityService.toggleLike(comment.id, 'comment');
      setLikes(result.liked ? likes + 1 : Math.max(0, likes - 1));
      setIsLiked(result.liked);
    } catch (error) {
      console.error(':', error);
    }
  };

  // 
  const handleReplyClick = () => {
    onReply(comment);
  };

  // 
  const handleEdit = () => {
    setEditing(true);
    setEditContent(comment.get('content') || '');
  };

  // 
  const handleSaveEdit = async () => {
    if (!editContent.trim()) return;

    setSaving(true);
    try {
      //  API
      // await CommunityService.updateComment(comment.id, { content: editContent });
      setEditing(false);
      onUpdate(); // 
    } catch (error) {
      console.error(':', error);
      alert(': ' + error.message);
    } finally {
      setSaving(false);
    }
  };

  // 
  const handleCancelEdit = () => {
    setEditing(false);
    setEditContent(comment.get('content') || '');
  };

  // 
  const handleDelete = async () => {
    if (!window.confirm('')) {
      return;
    }

    try {
      //  API
      // await CommunityService.deleteComment(comment.id);
      onUpdate(); // 
    } catch (error) {
      console.error(':', error);
      alert(': ' + error.message);
    }
  };

  //  Markdown
  const renderContent = () => {
    const content = comment.get('content') || '';
    
    return content
      .replace(/\*\*(.*?)\*\*/g, '<strong>$1</strong>')
      .replace(/\*(.*?)\*/g, '<em>$1</em>')
      .replace(/`(.*?)`/g, '<code>$1</code>')
      .replace(/\n/g, '<br>');
  };

  return (
    <div 
      className="comment-item"
      onMouseEnter={() => setShowActions(true)}
      onMouseLeave={() => setShowActions(false)}
    >
      {/*  */}
      <div className="comment-header">
        <div className="comment-author">
          <img 
            src={getAvatarUrl()} 
            alt=""
            className="comment-avatar"
          />
          <div className="author-info">
            <span className="author-name">{getUsername()}</span>
            {isAuthor() && (
              <span className="author-badge"></span>
            )}
            <span className="comment-time">
              {formatTime(comment.get('createdAt'))}
            </span>
          </div>
        </div>

        {/*  */}
        {showActions && (
          <div className="comment-actions">
            <button 
              className="action-btn reply-btn"
              onClick={handleReplyClick}
              title=""
            >
              
            </button>
            
            {isAuthor() && (
              <>
                <button 
                  className="action-btn edit-btn"
                  onClick={handleEdit}
                  title=""
                >
                  
                </button>
                <button 
                  className="action-btn delete-btn"
                  onClick={handleDelete}
                  title=""
                >
                  
                </button>
              </>
            )}
          </div>
        )}
      </div>

      {/*  */}
      <div className="comment-content">
        {editing ? (
          <div className="edit-comment">
            <textarea
              value={editContent}
              onChange={(e) => setEditContent(e.target.value)}
              rows="3"
              className="edit-textarea"
              maxLength="1000"
            />
            <div className="edit-actions">
              <button 
                onClick={handleCancelEdit}
                className="cancel-edit-btn"
                disabled={saving}
              >
                
              </button>
              <button 
                onClick={handleSaveEdit}
                className="save-edit-btn"
                disabled={!editContent.trim() || saving}
              >
                {saving ? '...' : ''}
              </button>
            </div>
          </div>
        ) : (
          <div 
            className="comment-text"
            dangerouslySetInnerHTML={{ __html: renderContent() }}
          />
        )}
      </div>

      {/*  */}
      <div className="comment-footer">
        <button 
          className={`like-btn ${isLiked ? 'liked' : ''}`}
          onClick={handleLike}
        >
          <span className="like-icon"></span>
          <span className="like-count">{likes}</span>
        </button>

        <button 
          className="reply-footer-btn"
          onClick={handleReplyClick}
        >
          
        </button>
      </div>

      {/*  */}
      {comment.get('parent') && (
        <div className="reply-indicator">
           <span className="reply-to">@{comment.get('parent')?.get('author')?.get('username') || ''}</span>
        </div>
      )}
    </div>
  );
};

export default CommentItem;