
// services/dataMigrationService.js
import AV from 'leancloud-storage';
import { UserService } from './userService';

/**
 *  - 
 */
export const DataMigrationService = {
  /**
   * 
   */
  setupAdminUser: async () => {
    try {
      console.log('...');
      
      const currentUser = AV.User.current();
      if (!currentUser) {
        throw new Error('');
      }
      
      console.log(':', currentUser.id);
      return {
        id: currentUser.id,
        username: currentUser.getUsername(),
        email: currentUser.getEmail(),
        nickname: currentUser.get('nickname')
      };
    } catch (error) {
      console.error(':', error);
      throw error;
    }
  },

  /**
   * 
   */
  migrateCategoriesToCurrentUser: async () => {
    try {
      console.log('...');
      
      const currentUser = AV.User.current();
      if (!currentUser) {
        throw new Error('');
      }
      
      const query = new AV.Query('Category');
      
      //  createdBy 
      query.doesNotExist('createdBy');
      const categories = await query.find();
      
      console.log(` ${categories.length} `);
      
      let migratedCount = 0;
      for (const category of categories) {
        try {
          category.set('createdBy', currentUser);
          
          //  ACL 
          const acl = new AV.ACL();
          acl.setReadAccess(currentUser, true);
          acl.setWriteAccess(currentUser, true);
          acl.setPublicReadAccess(true); // 
          category.setACL(acl);
          
          await category.save();
          migratedCount++;
          console.log(` : ${category.get('name')}`);
        } catch (error) {
          console.error(` ${category.id}:`, error);
        }
      }
      
      console.log(`: ${migratedCount}/${categories.length}`);
      return { migrated: migratedCount, total: categories.length };
    } catch (error) {
      console.error(':', error);
      throw error;
    }
  },

  /**
   * 
   */
  migrateQuestionsToCurrentUser: async () => {
    try {
      console.log('...');
      
      const currentUser = AV.User.current();
      if (!currentUser) {
        throw new Error('');
      }
      
      const query = new AV.Query('Question');
      
      //  createdBy 
      query.doesNotExist('createdBy');
      const questions = await query.find();
      
      console.log(` ${questions.length} `);
      
      let migratedCount = 0;
      for (const question of questions) {
        try {
          question.set('createdBy', currentUser);
          
          //  ACL 
          const acl = new AV.ACL();
          acl.setReadAccess(currentUser, true);
          acl.setWriteAccess(currentUser, true);
          acl.setPublicReadAccess(true); // 
          question.setACL(acl);
          
          await question.save();
          migratedCount++;
          console.log(` : ${question.get('title')}`);
        } catch (error) {
          console.error(` ${question.id}:`, error);
        }
      }
      
      console.log(`: ${migratedCount}/${questions.length}`);
      return { migrated: migratedCount, total: questions.length };
    } catch (error) {
      console.error(':', error);
      throw error;
    }
  },

  /**
   * 
   */
  performFullMigration: async () => {
    try {
      console.log(' ...');
      
      // 1. 
      const adminUser = await DataMigrationService.setupAdminUser();
      
      // 2. 
      const categoryResult = await DataMigrationService.migrateCategoriesToCurrentUser();
      
      // 3. 
      const questionResult = await DataMigrationService.migrateQuestionsToCurrentUser();
      
      console.log(' ');
      
      return {
        success: true,
        adminUser,
        categories: categoryResult,
        questions: questionResult,
        message: `: ${categoryResult.migrated}, ${questionResult.migrated}`
      };
    } catch (error) {
      console.error(':', error);
      return {
        success: false,
        error: error.message
      };
    }
  },

  /**
   * 
   */
  checkMigrationStatus: async () => {
    try {
      //  createdBy 
      const categoryQuery = new AV.Query('Category');
      categoryQuery.doesNotExist('createdBy');
      const orphanCategories = await categoryQuery.count();
      
      //  createdBy 
      const questionQuery = new AV.Query('Question');
      questionQuery.doesNotExist('createdBy');
      const orphanQuestions = await questionQuery.count();
      
      // 
      const totalCategories = await new AV.Query('Category').count();
      const totalQuestions = await new AV.Query('Question').count();
      
      return {
        orphanCategories,
        orphanQuestions,
        totalCategories,
        totalQuestions,
        migrationNeeded: orphanCategories > 0 || orphanQuestions > 0
      };
    } catch (error) {
      console.error(':', error);
      throw error;
    }
  },
  
  migrateQuestionCreatedBy: async () => {
    try {
      console.log(' Question  createdBy ...');
      
      const currentUser = AV.User.current();
      if (!currentUser) {
        throw new Error('');
      }
      
      const query = new AV.Query('Question');
      
      //  createdBy 
      query.doesNotExist('createdBy');
      const questions = await query.find();
      
      console.log(` ${questions.length}  createdBy `);
      
      let migratedCount = 0;
      for (const question of questions) {
        try {
          question.set('createdBy', currentUser);
          
          //  ACL 
          const acl = new AV.ACL();
          acl.setReadAccess(currentUser, true);
          acl.setWriteAccess(currentUser, true);
          acl.setPublicReadAccess(false);
          question.setACL(acl);
          
          await question.save();
          migratedCount++;
          console.log(`  createdBy: ${question.get('title')}`);
        } catch (error) {
          console.error(` ${question.id}:`, error);
        }
      }
      
      console.log(`Question createdBy : ${migratedCount}/${questions.length}`);
      return { migrated: migratedCount, total: questions.length };
    } catch (error) {
      console.error(' Question createdBy :', error);
      throw error;
    }
  },
  
  /**
   *  Question 
   */
  performQuestionMigrationOnly: async () => {
    try {
      console.log('  Question ...');
      
      const currentUser = AV.User.current();
      if (!currentUser) {
        throw new Error('');
      }
      
      //  Question
      const questionResult = await DataMigrationService.migrateQuestionCreatedBy();
      
      console.log(' Question ');
      
      return {
        success: true,
        questions: questionResult,
        message: `Question : ${questionResult.migrated}`
      };
    } catch (error) {
      console.error('Question :', error);
      return {
        success: false,
        error: error.message
      };
    }
  }
};