// src/services/dialogflowService.js
const PROXY_BASE_URL = 'http://localhost:3001';

class DialogflowService {
  constructor() {
    this.baseUrl = PROXY_BASE_URL;
    this.sessions = new Map();
  }

  // 
  async checkServerStatus() {
    try {
      console.log(' ...');
      const response = await fetch(`${this.baseUrl}/api/health`);
      const data = await response.json();
      console.log(' :', data);
      return response.ok;
    } catch (error) {
      console.error(' :', error.message);
      return false;
    }
  }

  // 
  async sendWelcome(userId) {
    console.log(' ...');
    console.log('ID:', userId);
    console.log('URL:', `${this.baseUrl}/api/dialogflow/welcome`);

    // 
    const isServerRunning = await this.checkServerStatus();
    if (!isServerRunning) {
      console.error(' ');
      return this.getFallbackWelcome();
    }

    try {
      console.log(' ...');
      
      const requestBody = {
        userId: userId
      };
      console.log(':', requestBody);

      const response = await fetch(`${this.baseUrl}/api/dialogflow/welcome`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(requestBody)
      });

      console.log(' :', response.status, response.statusText);

      if (!response.ok) {
        const errorText = await response.text();
        console.error(' HTTP :', response.status, errorText);
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const data = await response.json();
      console.log(' :', data);
      
      if (!data.success) {
        console.warn(' :', data.error);
        return this.getFallbackWelcome();
      }

      console.log(' ');
      return {
        text: data.text,
        intent: data.intent,
        success: true
      };

    } catch (error) {
      console.error(' :', error.message);
      console.error(':', error.stack);
      return this.getFallbackWelcome();
    }
  }

  // 
  async detectIntent(message, userId) {
    console.log(' ...');
    console.log(':', message);
    console.log('ID:', userId);

    // 
    const isServerRunning = await this.checkServerStatus();
    if (!isServerRunning) {
      console.error(' ');
      return this.getFallbackResponse(message);
    }

    try {
      console.log(' ...');
      
      const requestBody = {
        message: message,
        userId: userId
      };
      console.log(':', requestBody);

      const response = await fetch(`${this.baseUrl}/api/dialogflow/chat`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(requestBody)
      });

      console.log(' :', response.status, response.statusText);

      if (!response.ok) {
        const errorText = await response.text();
        console.error(' HTTP :', response.status, errorText);
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const data = await response.json();
      console.log(' :', data);
      
      if (!data.success) {
        console.warn(' :', data.error);
        return this.getFallbackResponse(message);
      }

      console.log(' ');
      return {
        text: data.text,
        intent: data.intent,
        confidence: data.confidence,
        parameters: data.parameters,
        success: true
      };

    } catch (error) {
      console.error(' :', error.message);
      return this.getFallbackResponse(message);
    }
  }

  // 
  getFallbackWelcome() {
    console.log(' ');
    return {
      text: ' \n\n \n \n \n \n\n',
      intent: 'welcome_fallback',
      success: true
    };
  }

  // 
  getFallbackResponse(message) {
    console.log(' ');
    const msg = message.toLowerCase();
    
    if (msg.includes('') || msg.includes('')) {
      return {
        text: ' ...',
        intent: 'categories_fallback',
        success: true
      };
    } else if (msg.includes('') || msg.includes('') || msg.includes('')) {
      return {
        text: ' ',
        intent: 'search_fallback',
        success: true
      };
    } else if (msg.includes('')) {
      return {
        text: ' ',
        intent: 'review_fallback',
        success: true
      };
    } else if (msg.includes('') || msg.includes('')) {
      return {
        text: ' ...',
        intent: 'stats_fallback',
        success: true
      };
    } else if (msg.includes('') || msg.includes('hello') || msg.includes('hi')) {
      return {
        text: ' ',
        intent: 'greeting_fallback',
        success: true
      };
    } else {
      return {
        text: ' ',
        intent: 'general_fallback',
        success: true
      };
    }
  }
}

export default new DialogflowService();