// components/community/CommentSection.jsx
import React, { useState, useEffect, useRef } from 'react';
import { CommunityService } from '../../services/communityService';
import CommentItem from './CommentItem';
import AV from 'leancloud-storage';
import './CommentSection.css';

const CommentSection = ({ postId }) => {
  const [comments, setComments] = useState([]);
  const [newComment, setNewComment] = useState('');
  const [loading, setLoading] = useState(false);
  const [submitting, setSubmitting] = useState(false);
  const [sortBy, setSortBy] = useState('newest'); // newest, oldest, popular
  const [replyingTo, setReplyingTo] = useState(null);
  const textareaRef = useRef(null);

  useEffect(() => {
    loadComments();
  }, [postId, sortBy]);

  const loadComments = async () => {
    setLoading(true);
    try {
      const commentsData = await CommunityService.getComments(postId);
      
      // 
      const sortedComments = [...commentsData].sort((a, b) => {
        switch (sortBy) {
          case 'oldest':
            return new Date(a.get('createdAt')) - new Date(b.get('createdAt'));
          case 'popular':
            return (b.get('likes') || 0) - (a.get('likes') || 0);
          case 'newest':
          default:
            return new Date(b.get('createdAt')) - new Date(a.get('createdAt'));
        }
      });

      setComments(sortedComments);
    } catch (error) {
      console.error(':', error);
    } finally {
      setLoading(false);
    }
  };

  const handleSubmitComment = async () => {
    if (!newComment.trim()) return;

    const currentUser = AV.User.current();
    if (!currentUser) {
      alert('');
      return;
    }

    setSubmitting(true);
    try {
      let parentId = null;
      if (replyingTo) {
        parentId = replyingTo.id;
      }

      await CommunityService.addComment(postId, newComment, parentId);
      
      // 
      setNewComment('');
      setReplyingTo(null);
      
      // 
      await loadComments();
      
      // 
      showSuccessMessage('');
    } catch (error) {
      console.error(':', error);
      alert(': ' + error.message);
    } finally {
      setSubmitting(false);
    }
  };

  const handleReply = (comment) => {
    setReplyingTo(comment);
    setNewComment(`@${comment.get('author')?.get('username') || ''} `);
    textareaRef.current?.focus();
  };

  const cancelReply = () => {
    setReplyingTo(null);
    setNewComment('');
  };

  const showSuccessMessage = (message) => {
    // 
    console.log(message);
  };

  const handleKeyDown = (e) => {
    if (e.key === 'Enter' && (e.ctrlKey || e.metaKey)) {
      handleSubmitComment();
    }
  };

  const getCommentCountText = () => {
    const count = comments.length;
    if (count === 0) return '';
    if (count === 1) return '1 ';
    return `${count} `;
  };

  return (
    <div className="comment-section">
      {/*  */}
      <div className="comment-header">
        <h4 className="comment-title">
           {getCommentCountText()}
        </h4>
        
        <div className="comment-controls">
          <select 
            value={sortBy}
            onChange={(e) => setSortBy(e.target.value)}
            className="sort-select"
          >
            <option value="newest"></option>
            <option value="oldest"></option>
            <option value="popular"></option>
          </select>
          
          <button 
            className="refresh-comments"
            onClick={loadComments}
            disabled={loading}
          >
            {loading ? '' : ''}
          </button>
        </div>
      </div>

      {/*  */}
      <div className="comment-input-section">
        {replyingTo && (
          <div className="reply-indicator">
            <span> @{replyingTo.get('author')?.get('username') || ''}</span>
            <button onClick={cancelReply}></button>
          </div>
        )}
        
        <div className="comment-input-wrapper">
          <textarea
            ref={textareaRef}
            value={newComment}
            onChange={(e) => setNewComment(e.target.value)}
            onKeyDown={handleKeyDown}
            placeholder={replyingTo ? '...' : '...'}
            rows="3"
            className="comment-textarea"
            maxLength="1000"
          />
          
          <div className="comment-input-footer">
            <div className="char-count">
              {newComment.length}/1000
            </div>
            
            <button
              onClick={handleSubmitComment}
              disabled={!newComment.trim() || submitting}
              className="submit-comment-btn"
            >
              {submitting ? (
                <>
                  <div className="submit-spinner"></div>
                  ...
                </>
              ) : (
                ''
              )}
            </button>
          </div>
        </div>
        
        <div className="comment-tips">
          <span className="tip">  Markdown </span>
          <span className="tip"> + Ctrl </span>
        </div>
      </div>

      {/*  */}
      <div className="comments-list">
        {loading ? (
          <div className="comments-loading">
            <div className="loading-spinner"></div>
            <span>...</span>
          </div>
        ) : comments.length === 0 ? (
          <div className="no-comments">
            <div className="no-comments-icon"></div>
            <h5></h5>
            <p></p>
          </div>
        ) : (
          comments.map(comment => (
            <CommentItem
              key={comment.id}
              comment={comment}
              onReply={handleReply}
              onUpdate={loadComments}
            />
          ))
        )}
      </div>
    </div>
  );
};

export default CommentSection;