// components/community/CreatePostModal.jsx
import React, { useState, useRef, useEffect } from 'react';
import { CommunityService } from '../../services/communityService';
import AV from 'leancloud-storage';
import './CreatePostModal.css';

const CreatePostModal = ({ onClose, onSuccess }) => {
  const [formData, setFormData] = useState({
    title: '',
    content: '',
    tags: '',
    isPublic: true
  });
  const [submitting, setSubmitting] = useState(false);
  const [errors, setErrors] = useState({});
  const [charCount, setCharCount] = useState({ title: 0, content: 0 });
  const [suggestedTags, setSuggestedTags] = useState([]);
  const textareaRef = useRef(null);

  const maxLengths = {
    title: 100,
    content: 5000
  };

  // 
  const popularTags = [
    'JavaScript', 'React', '', 'LeetCode', '', 
    '', 'TypeScript', 'Vue', 'CSS', 'Node.js',
    '', '', '', ''
  ];

  useEffect(() => {
    // 
    if (textareaRef.current) {
      textareaRef.current.style.height = 'auto';
      textareaRef.current.style.height = textareaRef.current.scrollHeight + 'px';
    }
  }, [formData.content]);

  useEffect(() => {
    // 
    if (formData.title.length > 5) {
      const matchedTags = popularTags.filter(tag =>
        formData.title.toLowerCase().includes(tag.toLowerCase())
      );
      setSuggestedTags(matchedTags.slice(0, 3));
    } else {
      setSuggestedTags([]);
    }
  }, [formData.title]);

  const validateForm = () => {
    const newErrors = {};

    if (!formData.title.trim()) {
      newErrors.title = '';
    } else if (formData.title.length < 5) {
      newErrors.title = '5';
    } else if (formData.title.length > maxLengths.title) {
      newErrors.title = `${maxLengths.title}`;
    }

    if (!formData.content.trim()) {
      newErrors.content = '';
    } else if (formData.content.length < 10) {
      newErrors.content = '10';
    } else if (formData.content.length > maxLengths.content) {
      newErrors.content = `${maxLengths.content}`;
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleInputChange = (field, value) => {
    setFormData(prev => ({ ...prev, [field]: value }));
    setCharCount(prev => ({ ...prev, [field]: value.length }));
    
    // 
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: '' }));
    }
  };

  const handleTagSuggestionClick = (tag) => {
    const currentTags = formData.tags.split(',').map(t => t.trim()).filter(t => t);
    if (!currentTags.includes(tag)) {
      const newTags = [...currentTags, tag].join(', ');
      handleInputChange('tags', newTags);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }

    const currentUser = AV.User.current();
    if (!currentUser) {
      alert('');
      return;
    }

    setSubmitting(true);
    try {
      const tagsArray = formData.tags
        .split(',')
        .map(tag => tag.trim())
        .filter(tag => tag)
        .slice(0, 5); // 5

      const postData = {
        title: formData.title.trim(),
        content: formData.content.trim(),
        tags: tagsArray,
        isPublic: formData.isPublic
      };

      const newPost = await CommunityService.createPost(postData);
      
      // 
      setSubmitting(false);
      onSuccess(newPost);
      
    } catch (error) {
      console.error(':', error);
      setErrors({ submit: error.message || '' });
      setSubmitting(false);
    }
  };

  const getTitleCharCountColor = () => {
    const ratio = charCount.title / maxLengths.title;
    if (ratio > 0.9) return '#ff6b6b';
    if (ratio > 0.7) return '#ffa726';
    return '#4ecdc4';
  };

  const getContentCharCountColor = () => {
    const ratio = charCount.content / maxLengths.content;
    if (ratio > 0.9) return '#ff6b6b';
    if (ratio > 0.7) return '#ffa726';
    return '#4ecdc4';
  };

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="create-post-modal" onClick={e => e.stopPropagation()}>
        <div className="modal-header">
          <div className="header-content">
            <h3> </h3>
            <p></p>
          </div>
          <button className="close-btn" onClick={onClose}>
            
          </button>
        </div>

        <form onSubmit={handleSubmit} className="post-form">
          {/*  */}
          <div className="form-group">
            <label htmlFor="post-title">
               *
              <span 
                className="char-count"
                style={{ color: getTitleCharCountColor() }}
              >
                {charCount.title}/{maxLengths.title}
              </span>
            </label>
            <input
              id="post-title"
              type="text"
              placeholder="..."
              value={formData.title}
              onChange={(e) => handleInputChange('title', e.target.value)}
              className={errors.title ? 'error' : ''}
              maxLength={maxLengths.title}
            />
            {errors.title && <span className="error-message">{errors.title}</span>}
          </div>

          {/*  */}
          <div className="form-group">
            <label htmlFor="post-content">
               *
              <span 
                className="char-count"
                style={{ color: getContentCharCountColor() }}
              >
                {charCount.content}/{maxLengths.content}
              </span>
            </label>
            <textarea
              ref={textareaRef}
              id="post-content"
              placeholder="... Markdown "
              value={formData.content}
              onChange={(e) => handleInputChange('content', e.target.value)}
              className={errors.content ? 'error' : ''}
              maxLength={maxLengths.content}
              rows="6"
            />
            {errors.content && <span className="error-message">{errors.content}</span>}
            
            {/*  */}
            <div className="format-tips">
              <span className="tip-title"></span>
              <span className="tip-item">****</span>
              <span className="tip-item">**</span>
              <span className="tip-item">``</span>
              <span className="tip-item">- </span>
            </div>
          </div>

          {/*  */}
          <div className="form-group">
            <label htmlFor="post-tags">
              
              <span className="optional"></span>
            </label>
            <input
              id="post-tags"
              type="text"
              placeholder="JavaScript,,LeetCode"
              value={formData.tags}
              onChange={(e) => handleInputChange('tags', e.target.value)}
            />
            
            {/*  */}
            {suggestedTags.length > 0 && (
              <div className="tag-suggestions">
                <span className="suggestions-label"></span>
                {suggestedTags.map(tag => (
                  <button
                    key={tag}
                    type="button"
                    className="tag-suggestion"
                    onClick={() => handleTagSuggestionClick(tag)}
                  >
                    {tag}
                  </button>
                ))}
              </div>
            )}
            
            <div className="tags-hint">
              5
            </div>
          </div>

          {/*  */}
          <div className="form-group">
            <label className="checkbox-label">
              <input
                type="checkbox"
                checked={formData.isPublic}
                onChange={(e) => handleInputChange('isPublic', e.target.checked)}
              />
              <span className="checkmark"></span>
              
            </label>
            <div className="privacy-hint">
              {formData.isPublic 
                ? ' '
                : ' '
              }
            </div>
          </div>

          {/*  */}
          {errors.submit && (
            <div className="submit-error">
              <span className="error-icon"></span>
              {errors.submit}
            </div>
          )}

          {/*  */}
          <div className="form-actions">
            <button 
              type="button" 
              onClick={onClose}
              className="cancel-btn"
              disabled={submitting}
            >
              
            </button>
            <button 
              type="submit" 
              className="submit-btn"
              disabled={submitting}
            >
              {submitting ? (
                <>
                  <div className="submit-spinner"></div>
                  ...
                </>
              ) : (
                ' '
              )}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default CreatePostModal;