// InitPage.jsx
import React, { useState } from 'react';
import { 
  generateSampleData, 
  clearAllData, 
  checkDataStatus, 
  syncProblemsFromNotion, 
  checkNotionConnection,
  createCommunityClasses,
  generateCommunitySampleData,
  clearCommunityData
} from '../services/initClasses';
import './InitPage.css';

const InitPage = () => {
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState('');
  const [dataStatus, setDataStatus] = useState(null);
  const [notionStatus, setNotionStatus] = useState(null);
  const [syncResult, setSyncResult] = useState(null);
  const [communityStatus, setCommunityStatus] = useState(null);
  const [activeTab, setActiveTab] = useState('notion'); // 'notion', 'data', 'community'

  // 
  const handleCheckStatus = async () => {
    setLoading(true);
    try {
      const status = await checkDataStatus();
      setDataStatus(status);
      setMessage(` : ${status.categoryCount} , ${status.questionCount} `);
    } catch (error) {
      setMessage(` : ${error.message}`);
    } finally {
      setLoading(false);
    }
  };

  // 
  const handleCheckCommunityStatus = async () => {
    setLoading(true);
    try {
      const status = {
        postCount: 0,
        commentCount: 0,
        likeCount: 0,
        followCount: 0,
        hasData: false
      };
      
      try {
        //  checkDataStatus 
        // 
        status.postCount = 0;
        status.commentCount = 0;
        status.likeCount = 0;
        status.followCount = 0;
        status.hasData = false;
        
        setMessage(` `);
      } catch (error) {
        setMessage(` ""`);
      }
      
      setCommunityStatus(status);
    } catch (error) {
      setMessage(` : ${error.message}`);
    } finally {
      setLoading(false);
    }
  };

  //  Notion 
  const handleCheckNotionConnection = async () => {
    setLoading(true);
    setMessage('');
    setNotionStatus(null);
    
    try {
      const result = await checkNotionConnection();
      setNotionStatus(result);
      
      if (result.success && result.connected) {
        setMessage(` Notion : ${result.database.title}`);
      } else {
        setMessage(` Notion : ${result.error || ''}`);
      }
    } catch (error) {
      setMessage(`  Notion : ${error.message}`);
    } finally {
      setLoading(false);
    }
  };

  //  Notion 
  const handleSyncFromNotion = async () => {
    if (!window.confirm(' Notion ')) {
      return;
    }
    
    setLoading(true);
    setMessage('');
    setSyncResult(null);
    
    try {
      const result = await syncProblemsFromNotion();
      setSyncResult(result);
      
      if (result.success) {
        setMessage(` ${result.message}`);
        // 
        await handleCheckStatus();
      } else {
        setMessage(` ${result.message}`);
      }
    } catch (error) {
      setMessage(` : ${error.message}`);
    } finally {
      setLoading(false);
    }
  };

  // 
  const handleGenerateData = async () => {
    setLoading(true);
    setMessage('');
    
    try {
      const result = await generateSampleData();
      setMessage(` ${result.message}`);
      // 
      await handleCheckStatus();
    } catch (error) {
      setMessage(` ${error.message}`);
    } finally {
      setLoading(false);
    }
  };

  // 
  const handleCreateCommunityClasses = async () => {
    setLoading(true);
    setMessage('');
    
    try {
      const result = await createCommunityClasses();
      if (result.Post.success && result.Comment.success && result.Like.success && result.Follow.success) {
        setMessage(` `);
      } else {
        // 
        const failedTables = [];
        if (!result.Post.success) failedTables.push('Post');
        if (!result.Comment.success) failedTables.push('Comment');
        if (!result.Like.success) failedTables.push('Like');
        if (!result.Follow.success) failedTables.push('Follow');
        
        if (failedTables.length > 0) {
          setMessage(` : ${failedTables.join(', ')}`);
        } else {
          setMessage(` `);
        }
      }
      // 
      await handleCheckCommunityStatus();
    } catch (error) {
      setMessage(` : ${error.message}`);
    } finally {
      setLoading(false);
    }
  };

  // 
  const handleGenerateCommunityData = async () => {
    setLoading(true);
    setMessage('');
    
    try {
      const result = await generateCommunitySampleData();
      setMessage(` ${result.message}`);
      // 
      await handleCheckCommunityStatus();
    } catch (error) {
      setMessage(` : ${error.message}`);
    } finally {
      setLoading(false);
    }
  };

  // 
  const handleClearData = async () => {
    if (!window.confirm('')) {
      return;
    }
    
    setLoading(true);
    setMessage('');
    
    try {
      const result = await clearAllData();
      setMessage(` ${result.message}`);
      // 
      await handleCheckStatus();
    } catch (error) {
      setMessage(` ${error.message}`);
    } finally {
      setLoading(false);
    }
  };

  // 
  const handleClearCommunityData = async () => {
    if (!window.confirm('')) {
      return;
    }
    
    setLoading(true);
    setMessage('');
    
    try {
      const result = await clearCommunityData();
      setMessage(` ${result.message}`);
      // 
      await handleCheckCommunityStatus();
    } catch (error) {
      setMessage(` : ${error.message}`);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="init-page">
      <header className="page-header">
        <h1> </h1>
        <p> CategoryQuestion  Notion </p>
      </header>

      {/*  */}
      <section className="tab-navigation">
        <div className="tabs">
          <button 
            className={`tab ${activeTab === 'notion' ? 'active' : ''}`}
            onClick={() => setActiveTab('notion')}
          >
             Notion 
          </button>
          <button 
            className={`tab ${activeTab === 'data' ? 'active' : ''}`}
            onClick={() => setActiveTab('data')}
          >
             
          </button>
          <button 
            className={`tab ${activeTab === 'community' ? 'active' : ''}`}
            onClick={() => setActiveTab('community')}
          >
             
          </button>
        </div>
      </section>

      {/* Notion  */}
    
      {activeTab === 'notion' && (
        <section className="notion-section">
          <h2> Notion </h2>
          <div className="action-buttons">
            <button 
              onClick={handleCheckNotionConnection}
              disabled={loading}
              className="btn btn-notion"
            >
                Notion 
            </button>
            
            <button 
              onClick={handleSyncFromNotion}
              disabled={loading}
              className="btn btn-sync"
            >
                Notion 
            </button>
          </div>

          {notionStatus && (
            <div className="notion-status">
              <h4>Notion </h4>
              <div className={`status-card ${notionStatus.connected ? 'connected' : 'disconnected'}`}>
                <div className="status-header">
                  <span className="status-indicator">
                    {notionStatus.connected ? '' : ''}
                  </span>
                  <strong>
                    {notionStatus.connected ? '' : ''}
                  </strong>
                </div>
                
                {notionStatus.connected && notionStatus.database && (
                  <div className="database-info">
                    <p><strong>:</strong> {notionStatus.database.title}</p>
                    <p><strong>:</strong> {notionStatus.database.properties.length} </p>
                    <p><strong>URL:</strong> 
                      <a href={notionStatus.database.url} target="_blank" rel="noopener noreferrer">
                        
                      </a>
                    </p>
                  </div>
                )}
                
                {!notionStatus.connected && (
                  <div className="error-info">
                    <p><strong>:</strong> {notionStatus.error}</p>
                    <p className="help-text">
                       
                    </p>
                  </div>
                )}
              </div>
            </div>
          )}

          {syncResult && (
            <div className="sync-result">
              <h4></h4>
              <div className="result-card">
                <div className="result-stats">
                  <div className="stat">
                    <span className="stat-label">:</span>
                    <span className="stat-value">{syncResult.data?.fetched || 0}</span>
                  </div>
                  <div className="stat">
                    <span className="stat-label">:</span>
                    <span className="stat-value">{syncResult.data?.saved || 0}</span>
                  </div>
                  <div className="stat">
                    <span className="stat-label">:</span>
                    <span className="stat-value">{syncResult.data?.mode || 'cloud'}</span>
                  </div>
                </div>
                
                {syncResult.data?.problems && syncResult.data.problems.length > 0 && (
                  <div className="synced-problems">
                    <h5>:</h5>
                    <ul>
                      {syncResult.data.problems.slice(0, 3).map((problem, index) => (
                        <li key={index}>
                          <span className="problem-title">{problem.title}</span>
                          <span className="problem-category">{problem.category}</span>
                        </li>
                      ))}
                    </ul>
                  </div>
                )}
              </div>
            </div>
          )}
        </section>
      )}

      {/*  */}
      {activeTab === 'data' && (
        <section className="data-management-section">
          <h2> </h2>
          <div className="action-buttons">
            <button 
              onClick={handleCheckStatus}
              disabled={loading}
              className="btn btn-info"
            >
               
            </button>
            
            <button 
              onClick={handleGenerateData}
              disabled={loading}
              className="btn btn-success"
            >
               
            </button>
            
            <button 
              onClick={handleClearData}
              disabled={loading}
              className="btn btn-danger"
            >
               
            </button>
          </div>

          {dataStatus && (
            <div className="status-info">
              <h4> </h4>
              <div className="status-grid">
                <div className="status-item">
                  <span className="label">:</span>
                  <span className="value">{dataStatus.categoryCount}</span>
                </div>
                <div className="status-item">
                  <span className="label">:</span>
                  <span className="value">{dataStatus.questionCount}</span>
                </div>
                <div className="status-item">
                  <span className="label">:</span>
                  <span className={`value ${dataStatus.hasData ? 'has-data' : 'no-data'}`}>
                    {dataStatus.hasData ? '' : ''}
                  </span>
                </div>
              </div>
            </div>
          )}
        </section>
      )}

      {/*  */}
      {activeTab === 'community' && (
        <section className="community-management-section">
          <h2> </h2>
          <p className="section-description">
            
          </p>
          
          <div className="action-buttons">
            <button 
              onClick={handleCheckCommunityStatus}
              disabled={loading}
              className="btn btn-info"
            >
               
            </button>
            
            <button 
              onClick={handleCreateCommunityClasses}
              disabled={loading}
              className="btn btn-primary"
            >
               
            </button>
            
            <button 
              onClick={handleGenerateCommunityData}
              disabled={loading}
              className="btn btn-success"
            >
               
            </button>
            
            <button 
              onClick={handleClearCommunityData}
              disabled={loading}
              className="btn btn-danger"
            >
               
            </button>
          </div>

          {communityStatus && (
            <div className="status-info">
              <h4> </h4>
              <div className="status-grid">
                <div className="status-item">
                  <span className="label">:</span>
                  <span className="value">{communityStatus.postCount}</span>
                </div>
                <div className="status-item">
                  <span className="label">:</span>
                  <span className="value">{communityStatus.commentCount}</span>
                </div>
                <div className="status-item">
                  <span className="label">:</span>
                  <span className="value">{communityStatus.likeCount}</span>
                </div>
                <div className="status-item">
                  <span className="label">:</span>
                  <span className="value">{communityStatus.followCount}</span>
                </div>
                <div className="status-item">
                  <span className="label">:</span>
                  <span className={`value ${communityStatus.hasData ? 'has-data' : 'no-data'}`}>
                    {communityStatus.hasData ? '' : ''}
                  </span>
                </div>
              </div>
            </div>
          )}

          <div className="community-classes-info">
            <h4> </h4>
            <div className="classes-grid">
              <div className="class-card">
                <h5> Post ()</h5>
                <ul>
                  <li>title - </li>
                  <li>content - </li>
                  <li>author - </li>
                  <li>tags - </li>
                  <li>likes - </li>
                  <li>views - </li>
                  <li>commentCount - </li>
                  <li>isPublic - </li>
                  <li>status - </li>
                </ul>
              </div>
              
              <div className="class-card">
                <h5> Comment ()</h5>
                <ul>
                  <li>content - </li>
                  <li>author - </li>
                  <li>post - </li>
                  <li>parent - </li>
                  <li>likes - </li>
                </ul>
              </div>
              
              <div className="class-card">
                <h5> Like ()</h5>
                <ul>
                  <li>user - </li>
                  <li>post - </li>
                  <li>comment - </li>
                </ul>
              </div>
              
              <div className="class-card">
                <h5> Follow ()</h5>
                <ul>
                  <li>follower - </li>
                  <li>following - </li>
                </ul>
              </div>
            </div>
          </div>
        </section>
      )}

      {/*  */}
      {loading && (
        <div className="loading-state">
          <div className="spinner"></div>
          <span>...</span>
        </div>
      )}

      {message && (
        <div className={`message ${message.includes('') ? 'error' : message.includes('') ? 'warning' : 'success'}`}>
          {message}
        </div>
      )}

      {/*  */}
      <div className="instructions">
        <h3> </h3>
        
        {activeTab === 'notion' && (
          <div className="instruction-item">
            <h4> Notion </h4>
            <ul>
              <li><strong>:</strong>  Notion API </li>
              <li><strong> Notion :</strong>  Notion </li>
              <li><strong>:</strong> </li>
              <li><strong>:</strong>  Notion Page ID </li>
            </ul>
          </div>
        )}

        {activeTab === 'data' && (
          <div className="instruction-item">
            <h4> </h4>
            <ul>
              <li><strong>:</strong> </li>
              <li><strong>:</strong> </li>
              <li><strong>:</strong> </li>
            </ul>
          </div>
        )}

        {activeTab === 'community' && (
          <div className="instruction-item">
            <h4> </h4>
            <ul>
              <li><strong>:</strong> PostCommentLikeFollow</li>
              <li><strong>:</strong> </li>
              <li><strong>:</strong> </li>
              <li><strong>:</strong> </li>
            </ul>
            
            <div className="community-features">
              <h5>:</h5>
              <ul>
                <li> </li>
                <li> </li>
                <li> </li>
                <li> </li>
                <li> </li>
              </ul>
            </div>
          </div>
        )}

        <div className="instruction-item">
          <h4> </h4>
          <div className="config-requirements">
            <div className="config-item">
              <strong>Notion :</strong>
              <span> NOTION_INTEGRATION_TOKEN  NOTION_DATABASE_ID</span>
            </div>
            <div className="config-item">
              <strong>:</strong>
              <span> LeanCloud </span>
            </div>
            <div className="config-item">
              <strong>:</strong>
              <span>Notion </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default InitPage;